// $midnight: #2c3e50;
$clouds: #ecf0f1;

.checkbox-arrow {
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  
  // Layout
  .row {
    display:flex;
    .col {
      flex:1;
      &:last-child {
        // margin-left: 1em;
        margin-bottom:20px;

      }
    }
  }
  /* Accordion styles */
  .tabs-container {
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 4px -2px rgba(0,0,0,0.5);

  }
  .tab {
    width: 100%;
    color: white;
    overflow: hidden;
    &-label {
      display: flex;
      justify-content: space-between;
      padding: 1em;
      background: $clr4;
      font-weight: bold;
      cursor: pointer;
      /* Icon */
      
      &:hover {
        background: darken($clr4, 10%);
      }
      &::after {
        content: "\276F";
        width: 1em;
        height: 1em;
        text-align: center;
        transition: all .35s;
      }

    }

    &-content {
      max-height: 0;
      padding: 0 1em;
      color: $clr3;
      background:$clr9;
      transition: all .35s;
    }
    &-close {
      display: flex;
      justify-content: flex-end;
      padding: 1m;
      font-size: 0.75em;
      background: $clr4;
      cursor: pointer;
      &:hover {
        background: darken($clr4, 10%);
      }
    }
  }
  
  // :checked
  .checkbox-arrow:checked {
    + .tab-label {
      background: darken($clr4, 10%);
      &::after {
        transform: rotate(90deg);
      }
    }
    ~ .tab-content {
      max-height: 100vh;
      padding: 1em;
    }
  }
  .host-list > li{
    margin-right: 6px;

    & > span:first-child {
      
      margin-right: 5px;
      font-family: lato-bold;
      font-size: 1rem;
      // letter-spacing: 2px;
    }
  
  }
    .guest-list > ul{
    margin-bottom: 10px;
    &>li span:first-child{
      margin-right: 5px;
      font-family: lato-bold;
      font-size: 1rem;
